@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
.header-wrapper {
    height: 80px;
    background: transparent;
    font-family: 'Montserrat', sans-serif;
}
.navbar {
  display: flex;
  justify-content: space-between;
}

.navbar-brand {
  margin: 12px 0 0 10%;
}

.navbar-menu {
  width: 25%;
}

.navbar-menu {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

a:-webkit-any-link {
  font-weight: 1000;
  text-decoration: none;
  color: #634832;
}

.btn-1 {
  letter-spacing: 0.05rem;
  transition: 0.5s all ease-in-out;
  position: relative;
  background: transparent;
}

.btn-1:before {
  border-top: 0.1rem solid#634832;
  border-bottom: 0.1rem solid #634832;
  display: block;
  position: absolute;
  background: transparent;
  content: '';
  top: -10px;
  bottom: -10px;
  left: 200%;
  right: 200%;
  transition: 0.48s all ease-in-out;
}

.btn-1:hover {
  letter-spacing: 0.4rem;
  font-weight: 1200;
}

.btn-1:hover:before {
  left: 0;
  right: 0;

}
.home-container {
  height: calc(100% - 80px);
}

.video-container {
  position: fixed;
  z-index: 0;
}

@media (min-aspect-ratio: 16/9) {
  .video-container {
    width: 100%;
    height: auto;
  }
}

@media (max-aspect-ratio: 16/9) {
  .video-container {
    width: auto;
    height: 100%;
  }
}

.afterEnded {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.afterEnded-image {
  /* transform: translateX(-20%); */
  /* position: relative;
    max-width: 100%; */
  text-align: center;
  -webkit-animation: coffee 2s ease-in-out;
          animation: coffee 2s ease-in-out;
  /* other keywords like "infinite alternate" can be useful here */
}

@-webkit-keyframes coffee {
  /* You could think of as "step 1" */
  0% {
    opacity: 0;
    transform: translateX(-3%);
  }

  /* You could think of as "step 2" */
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes coffee {
  /* You could think of as "step 1" */
  0% {
    opacity: 0;
    transform: translateX(-3%);
  }

  /* You could think of as "step 2" */
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.afterEnded-text {
  font-family: 'Bebas Neue', cursive;
  font-size: 128px;
  color: #634832;
  justify-self: center;
  -webkit-animation: title 2s ease-in-out;
          animation: title 2s ease-in-out;
  /* other keywords like "infinite alternate" can be useful here */
}

.afterEnded-text-subtitle {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  font-size: 28px;
}

.italic {
  font-style: italic;
  font-size: 24px;
  padding: 16px;
}

@-webkit-keyframes title {
  0% {
    opacity: 0;
    transform: translateX(-3%);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes title {
  0% {
    opacity: 0;
    transform: translateX(-3%);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

body {
    margin: 0;
}

html, body, #root, .app-container {
    height: 100%;
    background-color: rgba(236, 224, 209, 0.4);
}


